"use client";

import Link from "next/link";
import { BarChart2, Filter, Menu, Moon, Sun, X, HelpCircle } from "lucide-react";
import { useState, useEffect } from "react";
import { useTheme } from "next-themes";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";

interface NavbarProps {
    onOpenFilters?: () => void;
    activeFilterCount?: number;
}

export default function Navbar({ onOpenFilters, activeFilterCount = 0 }: NavbarProps) {
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const { theme, setTheme } = useTheme();
    const [mounted, setMounted] = useState(false);
    const pathname = usePathname();

    // Avoid hydration mismatch
    useEffect(() => {
        setMounted(true);
    }, []);

    const navLinks = [
        { name: "Dashboard", href: "/" },
        { name: "Watchlist", href: "/watchlist" },
        { name: "Strength Analyzer", href: "/strength-analyzer" },
        { name: "Insights", href: "/stocks" },
        { name: "Blog", href: "/blog" },
        { name: "How it Works", href: "/how-it-works" },
    ];

    // Hide Navbar on Auth pages (though they should be inaccessible now)
    if (pathname === "/signin" || pathname === "/signup") {
        return null;
    }

    return (
        <nav className="bg-card border-b border-border sticky top-0 z-30 transition-colors duration-300">
            <div className="w-full px-4 md:px-6">
                <div className="flex items-center justify-between h-16">
                    {/* Logo */}
                    <Link href="/" className="flex items-center gap-2">
                        <div className="bg-primary p-1.5 rounded-lg">
                            <BarChart2 className="w-6 h-6 text-primary-foreground" />
                        </div>
                        <span className="text-xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-primary to-cyan-500">
                            Trade Analyzer
                        </span>
                    </Link>

                    {/* Desktop Nav */}
                    <div className="hidden md:flex items-center gap-6">
                        {navLinks.map((link) => (
                            <Link
                                key={link.name}
                                href={link.href}
                                className={cn(
                                    "text-sm font-medium transition-colors hover:text-primary",
                                    pathname === link.href
                                        ? "text-primary"
                                        : "text-muted-foreground"
                                )}
                            >
                                {link.name}
                            </Link>
                        ))}
                    </div>

                    {/* Actions */}
                    <div className="flex items-center gap-3">

                        {/* Filter Button */}
                        {(pathname === "/" || pathname === "/watchlist" || pathname === "/stocks" || pathname === "/nifty100" || pathname === "/strength-analyzer") && onOpenFilters && (
                            <button
                                onClick={onOpenFilters}
                                className="relative p-2 text-muted-foreground hover:bg-accent hover:text-accent-foreground rounded-lg transition-colors flex items-center gap-2"
                            >
                                <Filter className="w-5 h-5" />
                                <span className="hidden sm:inline text-sm font-medium">Advanced Filters</span>
                                {activeFilterCount > 0 && (
                                    <span className="absolute -top-1 -right-1 bg-primary text-primary-foreground text-[10px] font-bold w-5 h-5 flex items-center justify-center rounded-full">
                                        {activeFilterCount}
                                    </span>
                                )}
                            </button>
                        )}

                        {/* Theme Toggle */}
                        {mounted && (
                            <button
                                onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
                                className="p-2 text-muted-foreground hover:bg-accent hover:text-accent-foreground rounded-lg transition-colors"
                                aria-label="Toggle Theme"
                            >
                                {theme === "dark" ? (
                                    <Sun className="w-5 h-5" />
                                ) : (
                                    <Moon className="w-5 h-5" />
                                )}
                            </button>
                        )}
                        {/* Mobile Menu Button */}
                        <button
                            className="md:hidden p-2 text-muted-foreground"
                            onClick={() => setIsMenuOpen(!isMenuOpen)}
                        >
                            {isMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
                        </button>
                    </div>
                </div>
            </div>

            {/* Mobile Menu */}
            {
                isMenuOpen && (
                    <div className="md:hidden border-t border-border bg-card">
                        <div className="px-4 py-4 space-y-4">
                            {navLinks.map((link) => (
                                <Link
                                    key={link.name}
                                    href={link.href}
                                    className="block text-sm font-medium text-muted-foreground hover:text-primary"
                                    onClick={() => setIsMenuOpen(false)}
                                >
                                    {link.name}
                                </Link>
                            ))}
                        </div>
                    </div>
                )
            }
        </nav >
    );
}
